	module.exports = {"data":{"wordpressPost":{"title":"Crimping: The 80&#8217;s Hair Style You Thought You&#8217;d Never See Again Is Back","content":"<p>Texture and crimping is definitely back, but not as you know it. According to ghd global ambassador Adam Reed, the modern-day version is softer and more subtle than back in the day, which means it&#8217;s infinitely more wearable.</p>\n<p>All the same &#8211; it&#8217;s still crimping.  And this is how to make it work.</p>\n<p>Firstly, says Adam, you need to ensure you don&#8217;t crimp ALL of your hair (so retro, and not in a good way).</p>\n<img class=\"alignnone size-medium wp-image-3309\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204346/Contour-look-38-199x300.jpg\" alt=\"\" width=\"199\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204346/Contour-look-38-199x300.jpg 199w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204346/Contour-look-38-768x1158.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204346/Contour-look-38-679x1024.jpg 679w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204346/Contour-look-38.jpg 952w\" sizes=\"(max-width: 199px) 100vw, 199px\" />\n<p>&nbsp;</p>\n<p>&#8220;Instead, experiment with different sections for a dual texture texture effect,&#8221; says Adam.</p>\n<p>&#8220;Crimping is one of those trends that everyone is scared of, but it never truly goes away.&#8221;</p>\n<p>Given that, we should probably all learn how to make it work.</p>\n<p>You can crimp a section of hair, as per Adam&#8217;s suggestion, or, if you just fancy some volume, crimp the roots of your hair. It provides amazing oomph and va va voom. That&#8217;s hair speak for big! And also, lovely.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3310\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204453/image001-6-197x300.jpg\" alt=\"\" width=\"197\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204453/image001-6-197x300.jpg 197w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204453/image001-6.jpg 444w\" sizes=\"(max-width: 197px) 100vw, 197px\" />\n<p>&nbsp;</p>\n<p>&#8220;Crimping is a session stylist&#8217;s secret to creating incredible volume and big hair,&#8221; reveals Adam.</p>\n<p>&#8220;We all carry a crimper in our kit as they&#8217;re incredible for adding volume and texture.&#8221;</p>\n<p>So now you know. Crimping. How about that?</p>\n<img class=\"alignnone size-medium wp-image-3311\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204545/XMAS17-Crimper-plates8-300x158.jpg\" alt=\"\" width=\"300\" height=\"158\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204545/XMAS17-Crimper-plates8-300x158.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204545/XMAS17-Crimper-plates8-768x404.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01204545/XMAS17-Crimper-plates8-1024x539.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"https://www.ghdhair.com/au/hair-crimper/ghd-contour-hair-crimper?CMP=AUTOAU-281&amp;CRE=BRAND&amp;TYPE=sponsoredsearch&amp;PLA=1&amp;gclid=EAIaIQobChMIlvfmv5_P1gIVnAQqCh1xpQ4XEAAYASAAEgL2wvD_BwE&amp;gclsrc=aw.ds&amp;dclid=CPDq-8Kfz9YCFc4plgod8AIO3g\">The ghd contour professional crimper, $169. </a></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/01202107/Contour-look-218.jpg"},"categories":[{"slug":"hair","name":"Hair"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"4ec6c875-653b-55a7-9f69-011d576fb7f0"}}

